export default {
	data() {
		return {
			ispass:false,
			phone:'',
			Code:'',
			password:'',
			passwords:'',
		};
	},
	components: {
		
	},
	computed: {
		
	},
	onLoad() {
		
	},
	methods: {
		addjiao(){
			if(this.password!==this.passwords){
				uni.showToast({
					title: '前后输入密码不一致',
					icon: 'none',
					duration: 1000
				});
				return false;
			};
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				let params = {
					'passWord':this.passwords,
					// "content":this.desc,
				}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api2 + '/rest-rp/user/'+landRegistLG.user.id, //接口地址。
					data: params,
					method: 'POST',
					header: {
						Authorization: "Bearer " + landRegistLG.token //将token放到请求头中
					},
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						uni.navigateBack({});
						// this.$store.commit('setMation', this.List); // 更新vuex
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			} 
			
		},
		
		addti(){
			if(this.phone==''){
				uni.showToast({
					title: '请输入手机号',
					icon: 'none',
					duration: 1000
				});
				return false;
			}
			if (!(/^1[34578]\d{9}$/.test(this.phone))) { // 校验手机号格式
				uni.showToast({
					title: '手机号码有误,请重填',
					icon: 'none',
					duration: 500
				});
				return false;
			};
			if (this.Code=='') { // 校验手机号格式
				uni.showToast({
					title: '手机号码有误,请重填',
					icon: 'none',
					duration: 500
				});
				return false;
			};
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				let params = {
					phone: this.phone,
					businessName: '1',
					phoneCode: this.Code,
					portal: 'iambuyer',
				}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api1 + '/rest-sso/phoneSmsVali', //接口地址。
					data: params,
					method: 'POST',
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						if(response.data.ret==200){
							this.ispass=true;
						}else{
							uni.showToast({
								title: '验证码已失效',
								icon: 'none',
								duration: 500
							});
							return false;
						}
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
		getcode(){
			if(this.phone==''){
				uni.showToast({
					title: '请输入手机号',
					icon: 'none',
					duration: 1000
				});
				return false;
			}
			if (!(/^1[34578]\d{9}$/.test(this.phone))) { // 校验手机号格式
				uni.showToast({
					title: '手机号码有误,请重填',
					icon: 'none',
					duration: 500
				});
				return false;
			};
			if (uni.getStorageSync('landRegist')) {
				let landRegistLG = JSON.parse(uni.getStorageSync('landRegist')); // 读取缓存的用户信息
				console.log(landRegistLG.user.id);
				let params = {
					phone: this.phone,
					businessName: '1',
					portal: 'iambuyer'
				}; // 请求总数居时 参数为空
				uni.showLoading({ // 展示loading
					title: '加载中'
				});
				uni.request({
					url: this.api1 + '/rest-sso/phoneSms', //接口地址。
					data: params,
					method: 'POST',
					success: (response) => {
						uni.hideLoading();
						console.log(response.data);
						if(response.data.ret==400){
							uni.showToast({
								title: response.data.msg,
								icon: 'none',
								duration: 1000
							});
							return false;
						}else if(response.data.ret==200){
							uni.showToast({
								title: '验证码已发送',
								icon: 'none',
								duration: 1000
							});
							return false;
						}
						
					},
					fail: (error) => {
						uni.hideLoading(); // 隐藏 loading
						uni.showToast({
							title: '网络繁忙，请稍后',
							icon: 'none',
							duration: 1000
						});
						console.log(error, '网络繁忙，请稍后');
					}
				});
			}
		},
	}
};
